              楚才学院与教育学院定期举办两院联合辩论比赛，旨在增强学生的思辨能力和创造能力，唇枪舌战，雄辩滔滔，丰富了课外生活，激发了学生自主思考的能力。2016年10月，楚才学院、教育学院第四届院辩论赛决赛在学院二号报告厅顺利举行，正反两方分别围绕“形成代沟的主要责任是父母还是子女”展开辩论。2020年12月，两院辩论赛决赛在110活动室顺利举行，围绕“选择高薪不喜欢还是低薪很喜欢的工作”的辩题展开辩论。辩论传统的延续，也是楚才学院对于智育探索的一种体现，而学院综合导师指导、荣誉课程学习等人才培养举措，更是学院长期以来对于综合能力培养模式的探索和优化而形成的系统性的、有力的智育举措，是具有楚才特色、湖大特色的的智育育人途径。
            </p>
