              <img class="cloud" src="../assets/红色祥云.png" alt="" />
              <span class="keywords">在体育方面</span
              >，学院以强健体魄、合作共赢为基调，依托学校体育文化节、学院“和”文化节等，鼓励学生积极参加体育赛事，开展趣味运动会。截至目前，学院已举办了六届“和”文化节，其中以拔河比赛、篮球友谊赛、趣味运动会为代表的一系列体育活动正在成为楚才学院的优良传统。学院积极响应“体育强国”的国家号召，在体育方面开拓进取，增强同学们的身体素质和团队合作意识，增进楚才各班级之间、各同学之间的互动与友谊。
