      <section style="margin-top: 150px">
        <div class="lp-section">
          <div class="content">
            <p class="body-text">
              <img class="cloud" src="../assets/红色祥云.png" alt="" />
              <span class="keywords">在美育方面</span
              >，学院高度重视美育教育并将美育教育渗透于学院工作的各个层面，从课外活动、校园文化、艺术展演等多个方面积极开展美育工作，落实落细，不断提高师生们的美育认识。联欢晚会、课外考察、红歌会、颁奖典礼、表彰仪式等多种活动丰富了楚才学子的课外生活。在湖北大学90周年生日即将到来之际，学院组织同学们走进湖北大学校史馆，加深同学们对母校湖大的了解，培养爱国强校的精神。楚才学子走出学校，前往武汉江夏区“东篱老屋”户外素质拓展基地开展主题为“青春恰遇十九大，砥砺奋进展年华”的党团日活动，参加素质拓展训练；前往武汉美术馆考察参观，重点浏览学习了该馆和南京博物院联合推出的年度重磅展览《大化惊高风——傅抱石作品展》和该馆主办的大型专题展览《艺术笔记：聂干因
              朱振庚
              钟孺乾水墨人物的画语文心》；前往武汉琴台音乐厅，欣赏“永恒的拉赫玛尼诺夫——盛原与武汉爱乐乐团新年音乐会”，共迎新年……
            </p>
            <p class="body-text">
              十年来，楚才学院的美育育人，不仅局限在学院，更是走出学院，走出学校。与国画、素描、印章以及各类艺术品近距离地接触，让楚才学子从思想上、精神上学思践悟，接受博大精深、深厚辽远的中华文化滋养。“做好美育工作是引导楚才学子树立正确的审美观、提升人文素养的重要举措，希望能激发同学们对美育的内涵和价值有更深的体悟。”常务副院长朱小梅指出，“我院始终围绕立德树人的根本任务，努力探索美育教育融入教育教学，今后学院将会继续拓展和深化美育类荣誉课程、外出考察等形式多样的育人平台，让楚才学子通过艺术文化的熏陶和滋养，进一步增强认识美、爱好美和创造美的能力，不断提升和完善自己的能力水平和综合素质，早日成为心灵丰富、灵魂丰盈、全面发展的真正人才。”
            </p>
