            <p class="body-text">
              <img class="cloud" src="../assets/红色祥云.png" alt="" />
              <span class="keywords">在劳育方面</span
              >，楚才学院率先在全校发起“美校园，迎双庆”劳动倡议，呼吁同学们知行合一，将劳动付诸实践，将实干内化于心，旨在倡导大家用例行劳动和勤劳的双手，共同美化校园，这既是新时代大学生需要上好的一门光荣的劳动课，更是一门自育成长、奋发有为的人生大课，各方协同、多维育人的社会大课，常讲常新、因时而进的时代大课。楚才学院以班级为单位，每周三和周五下午重点围绕田家炳楼周边和必要区域开展美化校园集体劳动。
            </p>
            <p class="body-text">
              疫情期间，楚才学院开展了以“爱国卫生运动——楚才学子校园美化”为主题的爱国卫生活动。在学院领导老师和班主任的带领和组织下，全体学生进行了学生宿舍环境的清洁工作，就学院全部办公室、110学生活动室以及田家炳大楼周边公共道路和区域（学子林）开展了集体劳动和清洁活动。
            </p>
            <p class="body-text">
              楚才学子通过劳动，以脚步践行爱国之心，以汗水浇灌文明之壤，发挥了爱国卫生运动在传染病防控和保障健康方面的积极作用，为保障全校师生的身体健康贡献楚才力量。“爱劳动，重卫生”已成为楚才师生良好的精神风貌。
            </p>
